/*  Skin Journeys Hero Banner
*
*  Version:
*    1.0.0 - 2022/01/12
*/

@import '../base.scss';

.c-skin-journeys {

  &__inner {
    background-color: $s-color--background-tertiary;
    margin-bottom: 11.3125rem;
  }

  &__wrapper {
    padding-top: 8rem;
    padding-bottom: 2.5rem
  }

  &__background-image-container {
    display: none;
  }

  &__left-container {
    text-align: center;
    width: 83%;
    margin: 0 auto;
  }

  &__pretitle {
    margin-bottom: 1.8125rem;
  }

  &__title {
    color: $s-color--font-6;
    margin-bottom: 1.0625rem;
    max-width: 40.25rem;
  }

  &__text {
    opacity: 0.6;
    max-width: 38.875rem;
    margin: 0 auto;
  }

  &__slides {
    margin-bottom: -12.125rem;
    margin-top: 3.125rem;
  }

  &__slides-wrapper:not(.is-initialised) {
    display: flex;
    flex-direction: row;
    overflow: hidden;

    & > a {
      width: 70%;
      flex-shrink: 0;
      min-height: 100%;
    }
  }

  &__slides-wrapper > a {
    cursor: pointer;
  }

  &__navigation {
    display: none;
  }

  &__article-wrapper {
    position: relative;
    transition: margin-top .3s ease-in;

    &.is-next-active,
    &.is-prev-active {
      margin-top: 1.1875rem;
    }
  }

  &__article-content {
    padding: 2.4375rem 0.9375rem 4rem 0.9375rem;
    position: absolute;
    inset: 0 0 0 0;
    text-align: center;
    color: $s-color--font-6;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__article-pretitle {
    color: $s-color--font-6;
  }

  &__article-btn {
    font-weight: 700;
    @include t-font--type-2;
    position: relative;
    display: inline-block;
    margin: 10% auto 0;
    width: max-content;

    &::before {
      content: "";
      height: 0.125rem;
      background-color: $s-color--font-6;
      width: 0;
      position: absolute;
      top: 100%;
      margin-top: 0.25rem;
      left: 0;
      transition: width 1s;
    }

    &:hover {
      text-decoration: none;

      &::before {
        width: 100%;
      }
    }

    &.has-arrow {
      color: $s-color--font-6;
      background: url('../../assets/icon-chevron-white.svg') no-repeat center right;
      padding-right: 1rem;
      background-size: 0.6875rem;
      background-position-y: 0.5rem;
    }
  }

  &__image-container {
    padding-bottom: 136%;
  }

  @include t-media-query($s-medium-up) {
    &__inner {
      position: relative;
    }

    &__wrapper {
      padding-bottom: 0;
    }

    &__background-image-container {
      display: block;
      height: 100%;
    }

    &__background-image-container-wrapper {
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      width: 43.5%;
    }

    &__wrapper {
      display: flex;
      justify-content: flex-end;
    }

    &__left-container {
      text-align: left;
      width: 38%;
      margin: 7.0625rem 5% 0 0;
    }

    &__pretitle {
      margin-bottom: 1.25rem;
    }

    &__title {
      margin-bottom: 1.25rem;
    }

    &__text {
      margin-left: 0;
    }

    &__slides {
      margin: 0;
      transform: translateY(4.4375rem);
      width: 50%;
      position: relative;
    }

    &__navigation {
      display: block;
    }

    &__next {
      position: absolute;
      content: "";
      background-image: url(../../assets/icon-arrow-next.svg);
      background-position: center;
      background-repeat: no-repeat;
      width: 4.4375rem;
      height: 4.4375rem;
      background-size: 1rem 1rem;
      border-radius: 50%;
      background-color: $s-color--background;
      right: 19%;
      bottom: 46%;
      z-index: 1;
      cursor: pointer;
    }

    &__article-wrapper {

      &.is-next-active,
      &.is-prev-active {
        margin-top: 0;
      }
    }

    &__article-title {
      margin-left: auto;
      margin-right: auto;
      width: 80%;
    }
  }

  @include t-media-query($s-large-up) {
    &__wrapper {
      padding-top: 10.75rem;
    }

    &__left-container {
      width: 36%;
    }

    &__pretitle {
      margin-bottom: 2.5rem;
    }

    &__title {
      margin-bottom: 1.5rem;
    }

    &__slides {
      width: 54.4%;
    }

    &__next {
      right: 17%;
      width: 5.4375rem;
      height: 5.4375rem;
    }

    &__image-container {
      padding-bottom: 112%;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__next {
      right: 19%;
    }
  }

  @include t-media-query($s-xxlarge-up) {

    &__wrapper {
      max-width: 125rem;
      margin: 0 auto;
    }

    &__left-container {
      width: 33.6%;
    }

    &__article-content {
      padding: 5.1875rem 1.25rem 5rem 1.25rem;
    }

    &__article-btn {
      margin-top: 8%;
    }
  }
}